@import url('https://fonts.googleapis.com/css2?family=Bitter:wght@800&display=swap');

html, body, #root {
  font: 0.9rem sans-serif;
  background: #0a1f44;
  color: #1e2432;
  height: 100%;
  margin: 0;
  background-image: url("./whether.jpg");
  background-size: cover;
  background-position: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.name {
  color: White;
  font-family: Monaco, monospace;
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 5px;
}

.main-container {
  background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) );
  background-image: url("./whether.jpg");
  background-size: cover;
  background-position: center;
 }

.main-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.search {
  outline: none;
  padding: 15px 7%;
  border-radius: 20px;
  border: none;
  margin-bottom: 3%;
  background: rgba(250, 250, 250, 0.6);
  font-weight: 500;
  text-align: center;
}

.city {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.city-weather {
  display:inline-flex;
  white-space: nowrap;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 40px 15%;
  backdrop-filter: blur(8px) saturate(200%);
  -webkit-backdrop-filter: blur(8px) saturate(200%);
  background-color: rgba(255, 255, 255, 0.52);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  margin-right: 5rem;
  margin-left: -1.8rem;
}

.city-air {
  display:inline-flex;
  white-space: nowrap;
  align-items: center;
  justify-content:center;
  flex-direction: column;
}

p {
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.city-temp {
  font-size: 5rem;
  font-weight: bold;
  margin-top: 10px;
  color: #1e2432;
  text-align: center;
}

.city sup {
  font-size: 0.5em;
}

.city-name {
  font-size: 2em;
}

.city-name sup {
  padding: 0.2em 0.6em;
  margin-left: 0.2em;
  border-radius: 30px;
  color: #fff;
  background: #ff8c00;
}

.city-icon {
  margin-top: 10px;
  width: 100px;
  height: 100px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.air-quality {
  display:inline;
  white-space: nowrap;
  align-items: center;
  justify-content:center;
  flex-direction: column;
  padding: 40px 12%;
  backdrop-filter: blur(8px) saturate(200%);
  -webkit-backdrop-filter: blur(8px) saturate(200%);
  background-color: rgba(255, 255, 255, 0.52);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  font-size: 21px;
}

.visibility {
  display:inline;
  white-space: nowrap;
  align-items: center;
  justify-content:center;
  flex-direction: column;
  padding: 40px 12%;
  backdrop-filter: blur(8px) saturate(200%);
  -webkit-backdrop-filter: blur(8px) saturate(200%);
  background-color: rgba(255, 255, 255, 0.52);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  font-size: 21px;
}

.wind-speed {
  display:inline;
  white-space: nowrap;
  align-items: center;
  justify-content:center;
  flex-direction: column;
  padding: 40px 10%;
  backdrop-filter: blur(8px) saturate(200%);
  -webkit-backdrop-filter: blur(8px) saturate(200%);
  background-color: rgba(255, 255, 255, 0.52);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  font-size: 21px;
}

@media only screen and (max-width: 620px) {
  .name {
    text-align: center;
    margin: 3rem 0 3rem 0;
  }

  .search {
      padding: 20px 15%;
  }

  .city-weather {
      padding: 40px 20%;
      margin: 12em 0 9em 0;
  }

  .city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow-y: scroll;
  }

  .main-container {
    overflow-y: hidden;
  }

  .city-air {
    padding: 0 5rem;
    margin: -8em 0;
  }

  .air-quality, .visibility {
    margin-bottom: 2px;
  }

  ::-webkit-scrollbar {
    display: none;
  }
}

